<template>
  <el-container>
    <el-aside class="nav-bg" style="width: auto">
      <el-menu :router="true" :default-active="defaultUrl" :unique-opened="true" :text-color="textColor"
               class="el-menu-vertical-demo" :collapse="isCollapse" :active-text-color="activeColor">
        <el-submenu :index="list.url+list.id" v-for="(list,index) in menuLists" :key="index">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span slot="title">{{list.name}}</span>
          </template>
          <el-menu-item style="padding:0;text-align: center;" :key="index" :index="'/Home/Index/'+data.url"
                        v-for="(data,index) in list.children">{{data.name}}
          </el-menu-item>

        </el-submenu>
      </el-menu>
    </el-aside>
   <router-view/>
  </el-container>
</template>

<script>
  export default {
    name: "home",
    data() {
      return {
        isCollapse: false,
        backgroundColor: "#263638",
        textColor: "#97a3aa",
        activeColor: "#ffffff",
        defaultUrl: "",
        menuLists: [
          {
            "id": 1,
            "name": "监控管理",
            "icon": "icon wb-desktop",
            "url": "",
            "weight": 1,
            "children": [{
              "id": 2,
              "name": "现场情况监控",
              "icon": "",
              "url": "MonitoringManage/Situations",
              "weight": 1
            }]
          }, {
            "id": 3,
            "name": "系统设置",
            "icon": "icon wb-desktop",
            "url": "",
            "weight": 2,
            "children": [
              {
                "id": 4,
                "name": "人员管理",
                "icon": "",
                "url": "SystemSetting/User",
                "weight": 1
              }, {
                "id": 5,
                "name": "通道管理",
                "icon": "",
                "url": "SystemSetting/Aisle",
                "weight": 2
              }, {
                "id": 6,
                "name": "设备管理",
                "icon": "",
                "url": "SystemSetting/Device",
                "weight": 3
              }, {
                "id": 7,
                "name": "角色管理",
                "icon": "",
                "url": "SystemSetting/Role",
                "weight": 4
              }, {
                "id": 8,
                "name": "权限管理",
                "icon": "",
                "url": "SystemSetting/Jurisdiction",
                "weight": 5
              }, {
                "id": 9,
                "name": "部门管理",
                "icon": "",
                "url": "SystemSetting/Department",
                "weight": 6
              }, {
                "id": 10,
                "name": "一般参数",
                "icon": "",
                "url": "SystemSetting/Parameters",
                "weight": 7
              },
            ]
          },
        ]
      }
    },
    mounted() {
      this.defaultUrl = this.$route.path;
    }
  }
</script>

<style scoped>


  .el-menu-vertical-demo {
    /*box-sizing: border-box;*/
    padding-top: 52px;
    width: 200px;
    background: url(../assets/img/nav_bg.png) no-repeat 0 100% #263638;
    background-size: contain;
  }

</style>



