import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login.vue'
import Home from '../components/Home.vue'
import Header from '../components/Headers.vue'
/*现场监控详情*/
import Situations from '../components/MonitoringManage/SituationsMonitoring.vue'
/*系统设置*/
import Aisle from '../components/SystemSetting/AisleManage.vue'
import Department from '../components/SystemSetting/DepartmentManage.vue'
import Device from '../components/SystemSetting/DeviceManage.vue'
import Jurisdiction from '../components/SystemSetting/JurisdictionManage.vue'
import Parameters from '../components/SystemSetting/Parameters.vue'
import Role from '../components/SystemSetting/RoleManage.vue'
import User from '../components/SystemSetting/UserManage.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: '登录',
      component: Login
    },
    {
      path: '/Home',
      name: '公用头部',
      component: Header,
      children: [
        {
          path: 'Index',
          name: '首页',
          component: Home,
          children: [
            {
              path: 'MonitoringManage/Situations',
              name: '现场监控',
              component: Situations,
            }, {
              path: 'SystemSetting/Aisle',
              name: '通道号管理',
              component: Aisle,
            }, {
              path: 'SystemSetting/Department',
              name: '部门管理',
              component: Department,
            }, {
              path: 'SystemSetting/Device',
              name: '设备管理',
              component: Device,
            }, {
              path: 'SystemSetting/Jurisdiction',
              name: '权限管理',
              component: Jurisdiction,
            }, {
              path: 'SystemSetting/Parameters',
              name: '一般参数',
              component: Parameters,
            }, {
              path: 'SystemSetting/Role',
              name: '角色管理',
              component: Role,
            },{
              path: 'SystemSetting/User',
              name: '人员管理',
              component: User,
            }
          ]
        },
      ]
    },


  ]
})



