<template>
  <el-main>
    <el-row :gutter="20">
      <el-col :span="16">
        <el-tree
          :data="department"
          :props="defaultProps"
          :show-checkbox="false"
          node-key="id"
          default-expand-all
          :highlight-current="true"
          :expand-on-click-node="true"
          :render-content="renderContent">
        </el-tree>
      </el-col>
      <el-col :span="8">

      </el-col>
    </el-row>
  </el-main>
</template>

<script>
  let id = 1000;
  export default {
    name: "department-manage",
    created() {
      document.title = "部门管理";
    },
    data() {
      return {
        department: [{
          id: 1,
          label: '一级 1',
          children: [{
            id: 4,
            label: '二级 1-1',
            children: [{
              id: 9,
              label: '三级 1-1-1'
            }, {
              id: 10,
              label: '三级 1-1-2'
            }]
          }]
        }],
        defaultProps:
          {
            children: 'children',
            label:'label',
            id:'id'
          }
      }
    },
    methods: {
      append(data) {
        const newChild = {id: id++, label: '请修改部门名称', children: []};
        if (!data.children) {
          this.$set(data, 'children', []);
        }
        data.children.push(newChild);
      },

      remove(node, data) {
        const parent = node.parent;
        const children = parent.data.children || parent.data;
        const index = children.findIndex(d => d.id === data.id);
        children.splice(index, 1);
      },
    //        <el-button style="font-size: 12px;" type="text" on-click={ () => this.remove(node, data) }>Delete</el-button>
      renderContent(h, {node, data, store}) {
        return (
          <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
          <span>
          <span>{node.label}</span>
        </span>
        <span>
        <el-button style="font-size: 12px;" type="text" on-click={ () => this.append(data) }>添加子部门</el-button>

        </span>
        </span>);
      }
    }
  }
</script>

<style scoped>

</style>



