<template>
  <el-container>
    <el-main style="background-color: #f1f4f5;padding-left: 30px;">
      <div id="container">
      <el-row :gutter="20">
        <el-col :span="24">
          <div class="panel panel-bordered">
            <div class="panel-heading">
              <h3 class="panel-title">筛选及功能项</h3>
            </div>
            <div class="panel-body">
              <div class="search-left color-content">
                <span class="state">状态</span>
                <div class="state-list">
                <span class="state-active" style="width: 86px;" @click="selectStatus($event)">
                  <i style="opacity: 1"></i>
                  <a>全部</a>
                </span>
                  <span class="" style="width: 64px;" @click="selectStatus($event)"><i style="opacity: 0;"></i>
                  <a>异常</a>
                </span>
                  <span class="" style="width: 64px;" @click="selectStatus($event)"><i style="opacity: 0;"></i><a>正常</a></span>
                </div>
              </div>
              <el-button size="small" style="margin-left: 20px;" @click="addDevice()"><i class="icon ml-plus"></i> 新增设备
              </el-button>

                <div class="time-list">
                  <span class="time-icon">
                    <i class="iconmar icon wb-calendar"></i>
                  </span>
                  <input id="timelist" type="text" placeholder="起始日期"  class="notice-time color-content font-14" lay-key="1">
                  <span class="overtime-icon" @click="emptyData(null,1)">
                    <i class="iconmar icon wb-close"></i>
                  </span>
                </div>

              <span style="margin-left:27%;">通道号 </span>
              <div class="search-right" style="width: 170px;display: inline-block;margin-left: 5px;">

                <el-select v-model="selectAisle" placeholder="请选择" >
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </div>
              <div style="width: 250px;display: inline-block;margin-left: 5px;">
                <el-input
                  placeholder="按设备ID、IP、厂商、类型查询"
                  v-model="searchMsg" style="font-size: 12px;">
                  <i slot="suffix" class="icon glyphicon glyphicon-search" style="font-size: 12px;margin-right: 5px;"></i>
                </el-input>
              </div>
              </div>
            </div>

        </el-col>
        <el-col :span="24">
          <div class="tablecontainer">
            <table class="table table-bordered table-striped" style="width: 100%;text-align: left">
              <thead>
              <tr class="tabletitle">
                <th>通道号</th>
                <th>设备ID</th>
                <th>设备IP</th>
                <th>厂商</th>
                <th>生产日期</th>
                <th>设备类型</th>
                <th>设备角色</th>
                <th class="text-nowrap">操作</th>
              </tr>
              </thead>
              <tbody>
              <tr>
                <td style="width:10%">1</td>
                <td style="width:15%">1</td>
                <td style="width:15%">1</td>
                <td style="width:15%">1</td>
                <td style="width:15%">1</td>
                <td style="width:10%">1</td>
                <td style="width:10%">1</td>
                <td style="width:8%" class="text-nowrap">
                  <el-tooltip class="item" effect="dark" content="编辑" placement="top">
                    <i class="icon wb-edit" style="margin-right: 10px;cursor: pointer" @click="editDevice()" aria-hidden="true"></i>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="删除" placement="top">
                    <i class="icon wb-close " style="cursor: pointer" aria-hidden="true" @click="deleteDevice()"></i>
                  </el-tooltip>
                </td>
              </tr>
              <tr>
                <td style="width:10%">1</td>
                <td style="width:15%">1</td>
                <td style="width:15%">1</td>
                <td style="width:15%">1</td>
                <td style="width:15%">1</td>
                <td style="width:10%">1</td>
                <td style="width:10%">1</td>
                <td style="width:8%" class="text-nowrap">
                  <el-tooltip class="item" effect="dark" content="编辑" placement="top">
                    <i class="icon wb-edit" style="margin-right: 10px;cursor: pointer" @click="editDevice()" aria-hidden="true"></i>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" content="删除" placement="top">
                  <i class="icon wb-close " style="cursor: pointer" aria-hidden="true" @click="deleteDevice()"></i>
                  </el-tooltip>
                </td>
              </tr>

              </tbody>
            </table>

          </div>
        </el-col>
      </el-row>
        <div style="background-color: #ffffff;margin-left: 1px;margin-right: 1px;position: relative;height: 65px;">
        <div style="position: relative;float: left;background-color: #ffffff;margin-left: 1px;padding: 20px 10px;" >
          共<span>1</span>项
        </div>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="100" style="float: right; position: relative;margin-right: 20px;">
        </el-pagination>
        </div>
      </div>
      <div id="floor" class="pop-up-windows" v-show="figureShow" @click="closeFigure"></div>
      <div id="deleted" class="tishi-windows" v-show="deleteShow">
        <div class="tishi-top" style="margin-top: 10px;">
          <i class="icon wb-bell" style="color:#f96868;font-size: 22px;"></i>
          <span>确定删除该设备？</span>
          <i class="icon wb-close" style="color:#f96868;position:relative;float: right;padding-right: 10px; cursor: pointer"></i>
          <!--<img src="../../img/close_red.png"></div> -->
        <div class="tishi-bottom">
          <span class="delete-span font-14" @click="deleteIcon">删除</span>
          <span class="cancel-span font-14" @click="closeFigure">取消</span>
        </div>
      </div>
      </div>
    </el-main>
    <el-dialog :title=title :visible.sync="dialogFormVisible" width="40%">
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item label="设备ID" class="el-col-10" style="margin-right: 40px;margin-left: 14px;">
          <el-input v-model="form.deviceId" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="设备IP" class="el-col-10">
          <el-input v-model="form.deviceIp" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="通道号" class="el-col-10" style="margin-right: 40px;margin-left: 14px;">
          <el-select v-model="form.aisleName" placeholder="请选择" style="width: 100%;">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="厂商" class="el-col-10">
          <el-select v-model="form.vendor" placeholder="请选择" style="width: 100%;">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备类型" class="el-col-10" style="margin-right: 40px;margin-left: 14px;">
          <el-select v-model="form.deviceType" placeholder="请选择" style="width: 100%;">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备角色" class="el-col-10">
          <el-select v-model="form.deviceRole" placeholder="请选择" style="width: 100%;">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择日期" class="el-col-10" style="margin-right: 40px;margin-left: 14px;">
          <el-col :span="24">
            <el-form-item >
              <!--<el-date-picker type="date" placeholder="选择日期" v-model="form.productime" style="width: 100%;"></el-date-picker>-->
              <div class="time-list" style="margin:0">
                  <span class="time-icon" style="top:9px">
                    <i class="iconmar icon wb-calendar"></i>
                  </span>
                <input id="timelist_" type="text" placeholder="起始日期"  class="notice-time color-content font-14" style="width: 302px;">
                <span class="overtime-icon" style="top:9px" @click="emptyData(null,2)">
                    <i class="iconmar icon wb-close"></i>
                  </span>
              </div>
            </el-form-item>
          </el-col>
        </el-form-item>
        <!--<el-form-item label="生产日期">-->
          <!--<el-date-picker type="date" placeholder="请选择生产日期" v-model="form.productime" style="width: 100%;"></el-date-picker>-->
        <!--</el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer " style="margin-right: 15px;">
        <el-button size="small" type="primary" style="margin-top:100px;margin-bottom: 20px;" @click="postDevice">提 交</el-button>
        <el-button size="small" class="btnclose" @click="dialogFormVisible = false">取 消</el-button>

      </div>
    </el-dialog>

  </el-container>

</template>

<script>
  import ElContainer from "../../../node_modules/element-ui/packages/container/src/main.vue";
  import ElForm from "../../../node_modules/element-ui/packages/form/src/form.vue";

  $(function() {
    //起始时间


//    //	结束时间日期
//    laydate.render({
//      elem: '#overtime',
//      type: "datetime",
//      theme: "#62a8ea",
//      done: function(value, date, endDate) {
//        //			console.log(value)
//        noticeedit.data.NOT_DATE_END = value;
//      }
//    });
  })

  export default {
    components: {
      ElForm,
      ElContainer},
    name: "device-manage",
    created() {
      document.title = "设备管理";
    },
    mounted(){
      //头部筛选时间控件
      laydate.render({
        elem: '#timelist',
        //type: "datetime",
        range: "至",
        theme: "#62a8ea",
        done: function(value, date, endDate) {
          //			console.log(value)
          this.timeStart = value.substring(0, value.indexOf('至') - 1);
          console.log(this.timeStart)
          this.timeEnd = value.substring(value.indexOf('至') + 2);
          console.log(this.timeEnd )
//        noticeMain.getNoticeList(0);
        }
      });


    },
    data() {
      return {
        figureShow:false,
        deleteShow:false,
        options:[],
        timeStart:"",
        timeEnd:"",
        selectAisle:"",
        searchMsg:"",
        title:"新增设备",
        dialogFormVisible: false,
        chooseProducTime: "",
        form: {
          deviceId: "",
          deviceIp: "",
          aisleName: "",
          vendor: "",
          deviceType: "",
          deviceRole: "",
          startTime:"",
          endTime:""
        },
        rules: {}
      }
    },
    methods: {
      postDevice() {
        this.dialogFormVisible = false;
        this.$message({
          type: 'success',
          message: '添加成功!'
        });
      },
      addDevice() {
        this.dialogFormVisible = true;
        this.$nextTick(function(){
          laydate.render({
            elem: '#timelist_',
            //type: "datetime",
            range: "至",
            theme: "#62a8ea",
            done: function(value, date, endDate) {
              //			console.log(value)
              this.timeStart = value.substring(0, value.indexOf('至') - 1);
              console.log(this.timeStart)
              this.timeEnd = value.substring(value.indexOf('至') + 2);
              console.log(this.timeEnd )
//        noticeMain.getNoticeList(0);
            }
          });
        })

      },
      editDevice(){
        this.dialogFormVisible=true;
        this.title="编辑设备"
      },
      deleteDevice() {
        this.deleteShow=true;
        this.figureShow=true;
      },
      closeFigure(){
        this.deleteShow=false;
        this.figureShow=false;
      },
      deleteIcon(){
        this.deleteShow=false;
        this.figureShow=false;
//        删除接口
      },

      selectStatus(e) {
//        this.data.STATE = status;
        //点击筛选列表状态时清空时间段的搜索条件
        //			hisList.emptyTimeSeek();
        //点击列表筛选状态时，清空时间周期筛选状态
        //			hisList.emptyTimePeriod();
        if($(e.target).parent().hasClass("state-active") == true) {

        } else {
          $(e.target).parent().siblings().removeClass("state-active");
          $(e.target).parent().siblings().animate({
            width: "64px"
          });
          $(e.target).parent().siblings().find("i").animate({
            opacity: 0
          });
          $(e.target).parent().addClass("state-active");
          $(e.target).parent().animate({
            width: "86px"
          });
          $(e.target).parent().find("i").animate({
            opacity: 1
          });
//          hisList.getHistyList(0);

//          hisList.getHistyList(0);
        }
      },
      emptyData(value,flag){
        if(flag==1){
          if($('#timelist').val().trim().length > 0) {
            $('#timelist').val('');
            this.timeStart = "";
            this.timeEnd = "";
          }
        }else{
          if($('#timelist_').val().trim().length > 0) {
            $('#timelist_').val('');
            this.form.startTime = "";
            this.form.endTime = "";
          }
        }

      },
    }
  }
</script>

<style scoped>
  .btnclose {
    color: #ffffff;
    background-color: #e4eaec;
    border: none;
  }

  .btnclose:hover {
    background-color: #ccd5db;
  }

  .state-active {
    background-color: #ccd5db;
  }

  .search-left .state-list span:nth-of-type(1) > i {
    background: url(../../assets/img/select_all.png) no-repeat 50% 50% #4d9ade;
    /*background-img: url("../../assets/img/selectAll.png") no-repeat  #4d9ade;;*/
    /*background-image: url("../../assets/img/selectAll.png");*/
    /*background-color:#4d9ade ;*/
  }

  .state {
    float: left;
    line-height: 35px;
  }

  .search-left .state-list span:nth-of-type(2) > i {
    background: url(../../assets/img/weijiedan.png) no-repeat 50% 50% #c78641;
  }

  .search-left .state-list span:nth-of-type(3) > i {
    background: url(../../assets/img/yijiedan.png) no-repeat 50% 50% #44a0ab;
  }

  .search-left .state-list span > i {
    display: inline-block;
    width: 22px;
    height: 22px;
    position: absolute;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    opacity: 0;
    left: 0;
  }

  .search-left .state-list {
    float: left;
    height: 35px;
    line-height: 35px;
    /* border: 1px solid #E8EDEF; */
    border-radius: 2px;
    margin-left: 5px;
    vertical-align: middle;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
  }

  .search-left {
    overflow: hidden;
  }

  .search-left {
    float: left;
  }

</style>



