<template>
  <el-container>
  <el-main style="background-color: #f1f4f5;padding-left: 30px;">
    <el-row :gutter="20">
      <el-col :span="24">
        <div class="panel panel-bordered">
          <div class="panel-heading">
            <h3 class="panel-title">筛选及功能项</h3>
          </div>
          <div class="panel-body">
            <div class="search-left color-content">
              <span class="state">状态</span>
              <div class="state-list">
                <span class="state-active" style="width: 86px;" @click="selectStatus($event,0)">
                  <i  style="opacity: 1;"></i>
                  <a >全部</a>
                </span>
                <span class="" style="width: 64px;" @click="selectStatus($event,1)"><i style="opacity: 0;"></i>
                  <a >异常</a>
                </span>
                <span class="" style="width: 64px;" @click="selectStatus($event,2)"><i style="opacity: 0;"></i><a >正常</a></span>
              </div>
            </div>
            <el-button size="small" style="margin-left: 20px;" @click="addAisle(null,1)"><i class="icon ml-plus"></i> 新增通道</el-button>
          </div>
        </div>
      </el-col>
      <el-col :span="24">
        <div class="tablecontainer">
        <table class="table table-bordered table-striped" style="width: 100%;text-align: left">
          <thead>
          <tr class="tabletitle">
            <th>通道号</th>
            <th>截屏卡IP地址</th>
            <th class="text-nowrap">操作</th>
          </tr>
          </thead>
          <tbody >
          <tr v-for="item in items">
            <td style="width:30%">{{item.channelId}}</td>
            <td style="width:60%">{{item.channelAddress}}</td>
            <td style="width:10%" class="text-nowrap">
              <el-tooltip class="item" effect="dark" content="编辑" placement="top">
                <i class="icon wb-edit" style="margin-right: 10px;cursor: pointer" aria-hidden="true" @click="editAisle(item,2)"></i>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="删除" placement="top">
                <i class="icon wb-close " style="cursor: pointer" aria-hidden="true" @click="deleteAisle(item.id)"></i>
              </el-tooltip>
            </td>
          </tr>
          </tbody>
        </table>
        </div>
      </el-col>
    </el-row>
    <div style="background-color: #ffffff;margin-left: 1px;margin-right: 1px;position: relative;height: 65px;">
      <div style="position: relative;float: left;background-color: #ffffff;margin-left: 1px;padding: 20px 10px;" >
        共<span>1</span>项
      </div>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="100" style="float: right; position: relative;margin-right: 20px;">
      </el-pagination>
    </div>

    <div id="floor" class="pop-up-windows" v-show="figureShow" @click="closeFigure"></div>
    <div id="deleted" class="tishi-windows" v-show="deleteShow">
      <div class="tishi-top" style="margin-top: 10px;">
        <i class="icon wb-bell" style="color:#f96868;font-size: 22px;"></i>
        <span>确定删除该通道？</span>
        <i class="icon wb-close" style="color:#f96868;position:relative;float: right;padding-right: 10px; cursor: pointer"></i>
        <!--<img src="../../img/close_red.png"></div> -->
        <div class="tishi-bottom">
          <span class="delete-span font-14" @click="deleteBtn()">删除</span>
          <span class="cancel-span font-14" @click="closeFigure">取消</span>
        </div>
      </div>
    </div>
  </el-main>
    <el-dialog :title=title :visible.sync="dialogFormVisible" width="40%" >
      <el-form :model="form">
        <el-form-item label="通道号" class="el-col-11" style="margin-right: 20px;margin-left: 14px;">
          <el-input  v-model="form.aisleName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="IP地址" class="el-col-11">
          <el-input  v-model="form.ipAdress" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer " style="margin-right: 15px;">
        <el-button size="small"  type="primary" @click="postAisle()" style="margin-top:20px">提 交</el-button>
        <el-button size="small" class="btnclose" @click="dialogFormVisible = false">取 消</el-button>

      </div>
    </el-dialog>
  </el-container>
</template>

<script>
  import ElContainer from "../../../node_modules/element-ui/packages/container/src/main.vue";
  import g from "../Common.vue"
  import axios from "axios"
  export default {
    components: {ElContainer},
    name: "aisle-manage",
    created() {
      document.title = "通道管理";
    },
    data(){
      return{
        flag:"",
        items:[],
        status:"",
        figureShow:false,
        deleteShow:false,
        title:"新增通道",
        formLabelWidth:'120px',
        dialogFormVisible:false,
        form:{
          aisleName:"",
          ipAdress:"",
          aisleId:""
        }
      }
    },
    mounted(){
      this.loadAisle();
    },
    methods:{
//      初始化获取列表
      loadAisle(){
        const self = this;
        axios({
          method: 'get',
          url: g.Url + '/channel/getAllChannel',
          contentType: 'application/json; charset=UTF-8',
        }).then(function (result) {
          console.log(result);
          self.items = result.data.data;
//            self.parentTotalPage=result.data.data.total/self.pageSize;
        }).catch(function (error) {
          console.log("error")
        });
      },
      deleteAisle(id){
         this.deleteShow=true;
         this.figureShow=true;
         this.form.id=id;
      },
      closeFigure(){
        this.deleteShow=false;
        this.figureShow=false;
      },
//      删除通道
      deleteBtn(){
        this.deleteShow=false;
        this.figureShow=false;
        const self = this;
        axios({
          method: 'delete',
          url: g.Url + '/channel/getAllChannel?channelId='+self.form.id,
          contentType: 'application/json; charset=UTF-8',
        }).then(function (result) {
          console.log(result);
          self.loadAisle();
//            self.parentTotalPage=result.data.data.total/self.pageSize;
        }).catch(function (error) {
          console.log("error")
        });
      },
      addAisle(){
        this.flag=1;
          this.dialogFormVisible=true;
      },
      editAisle(data){
        this.flag=2;
        this.title="编辑通道";
        this.dialogFormVisible=true;
        this.form.aisleName=data.channelId;
        this.form.ipAdress=data.channelAddress;
        this.form.aisleId=data.id;
      },
//      添加通道
      postAisle(){
        const self = this;
        self.dialogFormVisible=false;
        var url="";
        var data="";
//        新建
        if(self.flag==1){
          url="addChannel"
          data={
            channelId:self.form.aisleName,
            channelAddress:self.form.ipAdress
          }
        }else{
//          编辑
          url="updateChannel";
          data={
            id:self.form.id,
            channelId:self.form.aisleName,
            channelAddress:self.form.ipAdress
          }
        }
        axios({
          method: 'post',
          url: g.Url + '/channel/'+url,
          data: JSON.stringify(data),
          contentType: 'application/json; charset=UTF-8',
        }).then(function (result) {
          console.log(result);
          this.$message({
            type: 'success',
            message: '添加成功!'
          });
          self.loadAisle();
//            self.parentTotalPage=result.data.data.total/self.pageSize;
        }).catch(function (error) {
          console.log("error")
        });

      },
      selectStatus(e,status) {
        if($(e.target).parent().hasClass("state-active") == true) {

        } else {
          $(e.target).parent().siblings().removeClass("state-active");
          $(e.target).parent().siblings().animate({
            width: "64px"
          });
          $(e.target).parent().siblings().find("i").animate({
            opacity: 0
          });
          $(e.target).parent().addClass("state-active");
          $(e.target).parent().animate({
            width: "86px"
          });
          $(e.target).parent().find("i").animate({
            opacity: 1
          });
//          hisList.getHistyList(0);
        }
        var str="";
        switch(status){
          case 0:str="全部";break;
          case 1:str="异常";break;
          case 2:str="正常";break;
        }
        console.log(str);
//        const self=this;
//        axios({
//          method: 'get',
//          url: g.Url + '/channel/getChannelByStatus'+str,
//          contentType: 'application/json; charset=UTF-8',
//        }).then(function (result) {
//          console.log(result);
//          self.loadAisle();
////            self.parentTotalPage=result.data.data.total/self.pageSize;
//        }).catch(function (error) {
//          console.log("error")
//        });
      },
    }
  }

</script>

<style scoped>
.btnclose{
  color: #ffffff;
  background-color: #e4eaec;
  border:none;
}
.btnclose:hover{
  background-color: #ccd5db;
}
.state-active {
  background-color: #ccd5db;
}
.search-left .state-list span:nth-of-type(1)>i {
  background: url(../../assets/img/select_all.png) no-repeat 50% 50% #4d9ade;
  /*background-img: url("../../assets/img/selectAll.png") no-repeat  #4d9ade;;*/
  /*background-image: url("../../assets/img/selectAll.png");*/
  /*background-color:#4d9ade ;*/
}
.state {
  float: left;
  line-height: 35px;
}
.search-left .state-list span:nth-of-type(2)>i{
  background: url(../../assets/img/weijiedan.png) no-repeat 50% 50% #c78641;
}
.search-left .state-list span:nth-of-type(3)>i{
  background: url(../../assets/img/yijiedan.png) no-repeat 50% 50% #44a0ab;
}

.search-left .state-list span>i {
  display: inline-block;
  width: 22px;
  height: 22px;
  position: absolute;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  opacity: 1;
  left: 0;
}
.search-left .state-list {
  float: left;
  height: 35px;
  line-height: 35px;
  /* border: 1px solid #E8EDEF; */
  border-radius: 2px;
  margin-left: 5px;
  vertical-align: middle;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
}
.search-left {
  overflow: hidden;
}
.search-left {
  float: left;
}
</style>



